import React, { useState, useEffect, useContext } from "react"
import { useSelector, shallowEqual, useDispatch } from "react-redux"
import WishListModal from "./WishListModal"
import WishListMobile from "./WishListMobile"
import { I18nContext } from "../../../i18n"
import capitalize from "../../../functions/capitalize"
import { addFunctionWishList } from "../../../redux/actions/wishListActions.js"
import { setHTMLElementFixedPosition } from "../../../functions/Utilities.js"
import { toggleWishListAction } from "../../../redux/actions/wishListActions.js"

export default function WishListCustomModal() {
  const dispatch = useDispatch()
  const { translate, langCode } = useContext(I18nContext)

  const [showModal, setShowModal] = useState(false)

  const wishListState = useSelector(
    state => state.wishListReducer.wishlist,
    shallowEqual
  )

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )

  useEffect(() => {
    // This will set aria-hidden of html element based on the value of showModal
    setHTMLElementFixedPosition(showModal)
  }, [showModal])

  const LOCAL_STORAGE_WISHLIST_NAME = "demoStore.wishList"

  const handleOpen = () => setShowModal(true)

  const handleClose = () => setShowModal(false)

  useEffect(() => {
    let storedWishListString = localStorage.getItem(LOCAL_STORAGE_WISHLIST_NAME)
    let storedWishListObject = JSON.parse(storedWishListString)
    if (storedWishListObject)
      dispatch(addFunctionWishList([...storedWishListObject]))
  }, [])

  useEffect(() => {
    localStorage.setItem(
      LOCAL_STORAGE_WISHLIST_NAME,
      JSON.stringify(wishListState)
    )
  }, [wishListState])

  const toggleWish = (e, id, title, desc, currency_sign, image, price, url) => {
    e.preventDefault()
    dispatch(
      toggleWishListAction(
        id,
        title,
        desc,
        currency_sign,
        image,
        price,
        url,
        wishListState
      )
    )
  }

  const renderWishListWindow = () => {
    return (
      <WishListMobile
        show={showModal}
        close={handleClose}
        toggleWish={toggleWish}
      />
    )
  }

  return (
    <React.Fragment>
      {renderWishListWindow()}
      <div className="icon-container" onClick={handleOpen}>
        <div>
          <svg
            width="24"
            height="22"
            viewBox="0 0 24 22"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M9.00074 15.5537L8.27122 14.8217L8.25502 14.8055L8.23786 14.7902C5.88537 12.7022 3.99983 11.0049 2.69518 9.41224C1.39906 7.83002 0.75 6.43755 0.75 4.96833C0.75 2.57908 2.57657 0.75 4.95177 0.75C6.25825 0.75 7.57868 1.37168 8.43098 2.37899L9.00558 3.0581L9.57778 2.37698C10.4218 1.37235 11.7443 0.75 13.0518 0.75C15.4231 0.75 17.25 2.57872 17.25 4.96833C17.25 6.43941 16.6009 7.83189 15.3048 9.41401C14.0001 11.0067 12.1146 12.704 9.7619 14.794L9.74526 14.8088L9.72953 14.8245L9.00074 15.5537Z"
              stroke="#222E50"
              stroke-width="1.5"
            />
          </svg>
          {wishListState.length > 0 ? (
            <div>
              <span>{wishListState.length}</span>
            </div>
          ) : null}
        </div>
      </div>
    </React.Fragment>
  )
}
