import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import { Provider } from "react-redux"
import { I18nContextProvider } from "../i18n/index"
import { IntlProvider } from "react-intl"

import configureStore from "../redux/index.js"

import MailchimpSub from "../components/AC-Modals/MailchimpSub"
import MenuTopBar from "../shared/Header/BDMenuTopBar"
import NavMenu from "../shared/Header/BDNavMenu"
import Compare from "../shared/Header/Compare"

import Footer from "../shared/Footer/Footer"

import "../assets/css/menu.css"
import "../assets/css/minified.css"
import "../assets/css/avetti.css"
import "../assets/css/BDmailchimp.css"
import "../assets/css/sl.css"
import "../assets/css/b2b2c.css"
import "../assets/css/fontello.css"
import "../assets/jss/demo-ar.scss"

export const store = configureStore()

const Layout = ({ children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  return (
    <I18nContextProvider>
      <IntlProvider locale={"en"}>
        <Provider store={store}>
          <div>
            <MenuTopBar />
            <NavMenu />
            <Compare />
          </div>

          {children}
          <MailchimpSub />
          <Footer />
        </Provider>
      </IntlProvider>
    </I18nContextProvider>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
