import { call, put } from "redux-saga/effects"
import ParseErrorMessage from "../../functions/errorMessageParser"
import { store } from "../../other/layout"
import buttonsMapping from "../../functions/buttonsMapping.js"

import {
  FETCH_STORES_REQUEST,
  FETCH_STORES_REQUEST_SAGA,
  FETCH_STORES_SUCCESS,
  FETCH_STORES_FAILURE,
  SET_SELECTED_STORE,
} from "../types.js"

import { CATEGORY_FETCH_LINK } from "../links"

const fetchStoresRequestAction = () => ({
  type: FETCH_STORES_REQUEST,
})

const fetchStoresSuccessAction = (
  json,
  cid,
  cat,
  tempages,
  stores,
  priceButtonsTemp,
  //dynamicSlider,
  parents,
  link,
  filterUrl,
  urlFilterParams,
  resetCid,
  numberOfItems
) => {
  return {
    type: FETCH_STORES_SUCCESS,
    payload: {
      json,
      cid,
      cat,
      tempages,
      stores,
      priceButtonsTemp,
      // dynamicSlider,
      parents,
      link,
      filterUrl,
      urlFilterParams,
      resetCid,
      numberOfItems,
    },
  }
}

const fetchStoresFailureAction = err => ({
  type: FETCH_STORES_FAILURE,
  payload: err,
})

export const setSelectedStoreAction = payload => ({
  type: SET_SELECTED_STORE,
  payload: payload,
})

export const fetchStores = (cid, title) => {
  return dispatch => {
    // 72181 all products
    dispatch(fetchStoresRequestAction())
    dispatch(fetchStoresRequestSaga(cid, title, [], "store", null))
  }
}

export const fetchStoresRequestSaga = (
  cid,
  cat,
  parents,
  keyword,
  navCategory
) => ({
  type: FETCH_STORES_REQUEST_SAGA,
  payload: { cid, cat, parents, keyword, navCategory },
})

class LocalDataService {
  api = link =>
    fetch(link)
      .then(res => res.json())
      .then(json => {
        let tempages = []
        for (let i = 1; i <= Number(json[0].numOfPages); i++) {
          tempages.push(i)
        }
        let numberOfItems = Number(json[4].itemsCount)
        let stores = json[1].items.map(item => {
          const newObj = Object.assign({}, item)
          // update the new object
          let image = newObj.image
          newObj.image = image.replace("thumbnails", "images")
          return newObj
        })
        let priceButtonsTemp = buttonsMapping(json)
        //  let dynamicSlider = {};
        // dynamicSlider = mapDynamicFacetSliders(json);

        let urlFilterParams = ""
        console.error("DENEME CA", json, numberOfItems)
        return {
          json,
          tempages,
          stores,
          priceButtonsTemp,
          //   dynamicSlider,
          urlFilterParams,
          numberOfItems,
        }
      })
}

export function* fetchStoresSaga(action) {
  console.info("GIRDI2")
  try {
    let link = ""
    link = setLink(action.payload.cid, action.payload.cat)
    console.error("THU 1", link)
    let filterUrl = link
    let resetCid = action.payload.cid
    const api = new LocalDataService()

    console.info("GIRDI3", link)
    const store = yield call(api.api, link)
    console.info("GIRDI4", store)

    yield put(
      fetchStoresSuccessAction(
        store.json,
        action.payload.cid,
        action.payload.cat,
        store.tempages,
        store.stores,
        store.priceButtonsTemp,
        // store.dynamicSlider,
        action.payload.parents,
        link,
        filterUrl,
        store.urlFilterParams,
        resetCid,
        store.numberOfItems
      )
    )
  } catch (err) {
    console.error("ERROR", err)
    fetchStoresFailureAction(ParseErrorMessage(err))
  }
}

const setLink = (cid, title) => {
  let language = store.getState().mainReducer.lang
  let link = ""
  console.error("WED", language)
  if (title != "Stores")
    link = CATEGORY_FETCH_LINK.replace("$cid", cid) + `&Sellers=${title}`
  else link = CATEGORY_FETCH_LINK.replace("$cid", cid)

  link = link.replace("$LANGUAGE", language)
  console.error("WED", link)
  return link
}
