import {
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
  LOGOUT_SUCCESS,
  LOGOUT_FAILURE,
  UPDATE_LOGIN,
  UPDATE_LOGIN_FB,
  USER_DATA_SUCCESS,
  COOKIE_WINDOW_SHOW
} from "../types.js";

import {
  LOGOUT_LINK,
  LOGIN_CHECK_LINK,
  CATEGORY_FETCH_LINK
} from "../links.js";

export const cookieWindowShow = payload => ({
  type: COOKIE_WINDOW_SHOW,
  payload: payload
});

export const loginSuccess = json => ({
  type: LOGIN_SUCCESS,
  payload: {
    loginName: json.loginname,
    securityToken: json.securitytoken,
    lat: json.latitude,
    lng: json.longitude
  }
});

export const loginFailure = error => ({
  type: LOGIN_FAILURE,
  payload: error
});

export const logoutSuccess = () => ({
  type: LOGOUT_SUCCESS
});

export const logoutFailure = error => ({
  type: LOGOUT_FAILURE,
  payload: error
});

export const updateLoginInfo = json => ({
  type: UPDATE_LOGIN,
  payload: {
    loginName: json.loginname,
    securityToken: json.securitytoken,
    lat: json.latitude,
    lng: json.longitude
  }
});
export const updateLoginInfoFb = json => ({
  type: UPDATE_LOGIN_FB,
  payload: {
    loginName: json.name,
    securityToken: json.accessToken
  }
});

export const fetchLogin = securitytoken => {
  var form = new FormData();
  form.append("securitytoken", securitytoken);

  return dispatch => {
    fetch(LOGIN_CHECK_LINK, {
      headers: {
        Accept: "*/*"
      }
    })
      .then(res => res.json())
      .then(json => {
        console.error("LOGIN", json.__Success);
        dispatch(loginSuccess(json.__Result));
      })
      .catch(error => dispatch(loginFailure(error)));
  };
};

export const loginUserDataSuccess = data => ({
  type: USER_DATA_SUCCESS,
  payload: data
});

export const fetchUserData = (securitytoken, langCode) => {
  var form = new FormData();
  form.append("securitytoken", securitytoken);

  return dispatch => {
    fetch("https://ipapi.co/json", {
      headers: {
        Accept: "*/*"
      }
    })
      .then(res => res.json())
      .then(json => {
        let json2 = {
          __Result: {
            city: "Barrie",
            companyname: "",
            countrycode: "CA",
            countryname: "Canada",
            customerProps: [],
            latitude: 44.38909912109375,
            loggedin: false,
            loginname: "",
            longitude: -79.69010162353516,
            postal: "L4N",
            regioncode: "ON",
            regionname: "Ontario",
            securitytoken: ""
          },
          __Success: "true"
        };

        localStorage.setItem(
          "userLocation",
          JSON.stringify({
            city: json.city,
            countrycode: json.country_code,
            countryname: json.country_name,
            latitude: json.latitude,
            longitude: json.longitude,
            postal: json.postal,
            regioncode: json.region_code,
            regionname: json.region,
            languages: json.languages
          })
        );
        localStorage.setItem("userLocationManual", true);
        dispatch(
          loginUserDataSuccess({
            city: json.city,
            countrycode: json.country_code,
            countryname: json.country_name,
            latitude: json.latitude,
            longitude: json.longitude,
            postal: json.postal,
            regioncode: json.region_code,
            regionname: json.region,
            languages: json.languages
          })
        );
      })
      .catch(error => dispatch(loginFailure(error)));
  };
};

export const handleLogout = securitytoken => {
  return dispatch => {
    var form = new FormData();
    form.append("securitytoken", securitytoken);

    fetch(LOGOUT_LINK, {
      method: "POST",
      body: form,
      headers: {
        Accept: "*/*",
        credentials: "same-origin"
      },
      mimeType: "multipart/form-data",
      data: form
    }).then(json => {
      console.error("TEST FORM", json);
      dispatch(logoutSuccess());
    });
  };
};
