import React, { useState, useEffect, useContext } from "react";
import { useSelector, shallowEqual, useDispatch } from "react-redux";

import { LOGIN_CHECK_LINK, LOGOUT_LINK } from "../../redux/links.js";

import { updateLoginInfo } from "../../redux/actions/loginActions.js";

import classes from "./Styles/Loggedin.module.css";

const Loggedin = () => {
  const dispatch = useDispatch();
  const [isLoading, setisLoading] = useState(false);

  const loginInfoState = useSelector(
    state => state.loginReducer.loginName,
    shallowEqual
  );
  const securityInfoState = useSelector(
    state => state.loginReducer.securityToken,
    shallowEqual
  );

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  );

  const fetchJson = async url => {
    const response = await fetch(url, {
      headers: {
        Accept: "*/*"
      }
    });
    return response.json();
  };

  const logout = event => {
    event.preventDefault();
    setisLoading(true);
    var form = new FormData();
    form.append("securitytoken", securityInfoState);

    fetch(LOGOUT_LINK, {
      method: "POST",
      body: form,
      headers: {
        Accept: "*/*",
        credentials: "same-origin"
      },
      mimeType: "multipart/form-data",
      data: form
    })
      .then(res => res.json())
      .then(json => console.error("TEST FORM", json))

      // It parses the output
      .catch(function(error) {
        console.log("error---", error);
      })
      .then(() => {
        console.error("LOGOUT CLICKED");
        fetchJson(LOGIN_CHECK_LINK).then(json => {
          sessionStorage.setItem("UserData", JSON.stringify(json.__Result));
          if (json.__Result.loggedin == true) {
            dispatch(updateLoginInfo(json.__Result));
          } else {
            dispatch(updateLoginInfo(json.__Result));
          }
        });
      })
      .then(() => {
        setisLoading(false);
      });
  };
  return (
    <React.Fragment>
      <div className={classes.loggedinWrapper}>
        {isMobileState ? (
          <React.Fragment>
            <span className={classes.myAccount2} onClick={logout}>
              MY PROFILE
            </span>
          </React.Fragment>
        ) : (
          <React.Fragment>
            <span className={classes.hello}>Hi, {loginInfoState}</span>
            <span className={classes.myAccount} onClick={logout}>
              My Account
            </span>
          </React.Fragment>
        )}
      </div>
    </React.Fragment>
  );
};

export default Loggedin;
