import React, { useState, useEffect, useContext } from "react"
import { useSelector, shallowEqual } from "react-redux"
import { PROJECT_LINK, PREVIEW, VID } from "../../project-config"
import useWindowDimensions from "../../functions/useWindowDimensions"
import { FormattedNumber } from "react-intl"
import Button from "@material-ui/core/Button"
import { useLocation } from "@reach/router"

import { I18nContext } from "../../i18n"
export default function MiniCart({ close }) {
  const location = useLocation()

  const { translate, currency, priceConvert, langCode } = useContext(
    I18nContext
  )
  const { height, width } = useWindowDimensions()
  const [isLocal, setisLocal] = useState("")

  const [quotesExpanded, setQuotesExpanded] = useState(true)

  // useEffect(() => {
  //   if (basketProductsState && basketProductsState.length > 0) {
  //     setShoppingCartActive(true);
  //   } else if (
  //     basketQuoteProductsState &&
  //     basketQuoteProductsState.length > 0
  //   ) {
  //     setShoppingCartActive(false);
  //   }
  // }, []);

  const basketCountState = useSelector(
    state => state.sessionReducer.basket.itemsCount,
    shallowEqual
  )
  const basketProductsState = useSelector(
    state => state.sessionReducer.basket.products,
    shallowEqual
  )

  const basketQuoteProductsState = useSelector(
    state => state.sessionReducer.basket.quoteproducts,
    shallowEqual
  )
  const totalPriceProductsState = useSelector(
    state => state.sessionReducer.basket.totalPriceProducts,
    shallowEqual
  )

  const totalPriceQuoteProductsState = useSelector(
    state => state.sessionReducer.basket.totalPriceQuoteProducts,
    shallowEqual
  )

  const languageState = useSelector(
    state => state.mainReducer.lang,
    shallowEqual
  )

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )

  useEffect(() => {
    if (location.host.includes("localhost")) {
      setisLocal(`${PROJECT_LINK}/basket.html?vid=${VID}&iu=${languageState}`)
    } else {
      setisLocal(`${PREVIEW}/basket.html?vid=${VID}&iu=${languageState}`)
    }
  }, [])

  const handleEditIconClicked = (event, quoteBasket) => {
    event.stopPropagation()
    let urlToBasket = `${isLocal}${
      quoteBasket ? "&groupmode=quote" : "&groupmode=checkout"
    }`

    if (basketQuoteProductsState.length > 0) {
      typeof window !== "undefined" ? window.open(urlToBasket, "_blank") : null
    } else {
      location.replace(urlToBasket)
    }
  }

  const renderQuoteRequests = () => {
    if (basketQuoteProductsState.length > 0) {
      return (
        <React.Fragment>
          <div
            style={{
              backgroundColor: "#666",
              color: "white",
              cursor: "pointer",
            }}
            onClick={event => {
              event.stopPropagation()
              setQuotesExpanded(!quotesExpanded)
            }}
          >
            <h6 className="minicart-title">
              {translate("QuoteRequests")}
              <i
                className="material-icons"
                style={{ cursor: "pointer", fontSize: "38px" }}
              >
                keyboard_arrow_down
              </i>
            </h6>
          </div>
          <div
            className={`quote-requests-wrapper${
              quotesExpanded ? ` expanded` : ``
            }`}
          >
            {basketQuoteProductsState.map(
              ({ imageurl, title, price, qty, distName }, index) => {
                return (
                  <li>
                    <p className="minicart-item-distname">{distName}</p>
                    <p>{title.replace("&quot;", '"')}</p>
                    <div className="cd-price">
                      <p className="currency-p">
                        <FormattedNumber
                          value={price / priceConvert}
                          style="currency"
                          currency={currency}
                          children={result => <b>{result}</b>}
                        />

                        <b style={{ float: "right" }}>{qty}</b>
                      </p>
                    </div>
                    <i
                      onClick={event => handleEditIconClicked(event, true)}
                      className="material-icons minicart-item-edit-icon"
                    >
                      edit
                    </i>
                  </li>
                )
              }
            )}
            {renderTotalPriceSection(true)}
            <div className="continue-check text-center">
              <span>
                <Button
                  onClick={event => event.stopPropagation()}
                  target={basketQuoteProductsState.length > 0 ? "_blank" : ""}
                  href={isLocal + "&groupmode=quote"}
                  style={{
                    borderRadius: 0,
                  }}
                  variant="contained"
                  color="primary"
                  className={"checkout-btn active"}
                >
                  {translate("Continue")}
                </Button>
              </span>
            </div>
          </div>
        </React.Fragment>
      )
    } else {
      return null
    }
  }

  const renderMiniCartContent = () => {
    return (
      <React.Fragment>
        {basketProductsState.length === 0 ? (
          <span className="minicart-count">{translate("Emptycart")}</span>
        ) : null}

        <ul className="cd-cart-items" onClick={e => e.stopPropagation()}>
          {basketProductsState.length > 0
            ? basketProductsState.map(
                ({ imageurl, title, price, qty, distName, itemcode }) => {
                  return (
                    <li className="mini-listitem">
                      <img
                        className="cart-image"
                        src={`https://bdadmin3qa.avetti.io/store/${process.env.GATSBY_MCW_VID}/assets/items/thumbnails/${itemcode}.jpg`}
                      ></img>
                      <div className="inner-minicart">
                        <p className="minicart-item-distname">By {distName}</p>
                        <p>{title.replace("&quot;", '"')}</p>
                        <div className="cd-price">
                          <p className="currency-p">
                            <FormattedNumber
                              value={price / priceConvert}
                              style="currency"
                              currency={currency}
                              children={result => <b>{result}</b>}
                            />
                            <b className="cd-qty">
                              Qty.
                              {qty}
                            </b>
                          </p>
                          <div className="edit-section minicart-item-edit-icon">
                            <i
                              onClick={event =>
                                handleEditIconClicked(event, false)
                              }
                              className="material-icons minicart-item-edit-icon"
                            >
                              edit
                            </i>
                            Edit
                          </div>
                        </div>
                      </div>
                    </li>
                  )
                }
              )
            : null}
          {basketQuoteProductsState.length > 0
            ? renderTotalPriceSection()
            : null}
          {/* {basketQuoteProductsState.length > 0 ? renderCheckoutButton() : null}
          {renderQuoteRequests()} */}
        </ul>
      </React.Fragment>
    )
  }

  const renderTotalPriceSection = (renderForQuote = false) => {
    if (basketProductsState.length > 0 && !renderForQuote) {
      return (
        <div
          className="cd-cart-total"
          style={{
            backgroundColor: "#222E50",
            color: "white",
            position: basketQuoteProductsState.length === 0 ? "fixed" : "",
          }}
        >
          <p>
            <b>
              {translate("vm.myaccounthome_total")}
              <span>
                <FormattedNumber
                  value={totalPriceProductsState / priceConvert}
                  style="currency"
                  currency={currency}
                />
              </span>
            </b>
          </p>
        </div>
      )
    } else if (basketQuoteProductsState.length > 0 && renderForQuote) {
      return (
        <div
          className={"cd-cart-total quote"}
          style={{ backgroundColor: "#4e4e4e", color: "white" }}
        >
          <p>
            <b>
              {translate("vm.myaccounthome_total")}
              <span>
                <FormattedNumber
                  value={totalPriceQuoteProductsState / priceConvert}
                  style="currency"
                  currency={currency}
                />
              </span>
            </b>
          </p>
        </div>
      )
    } else {
      return null
    }
  }

  const renderCheckoutButton = () => {
    return (
      <div className="continue-check text-center">
        <span>
          <Button
            onClick={event => event.stopPropagation()}
            target={basketQuoteProductsState.length > 0 ? "_blank" : ""}
            href={isLocal + "&groupmode=checkout"}
            style={{
              borderRadius: 0,
            }}
            variant="contained"
            color="primary"
            className={
              "checkout-btn" +
              `${basketProductsState.length > 0 ? " active" : ""}`
            }
            disabled={basketProductsState.length !== 0 ? false : true}
          >
            {basketProductsState.length !== 0
              ? translate("ContinuetoCheckout")
              : translate("Continue")}
          </Button>
        </span>
      </div>
    )
  }

  return (
    <>
      <div
        style={isMobileState ? { width: width } : { width: "768px" }}
        className={langCode === "ar" ? "rtl" : ""}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            height: height,
            justifyContent: "space-between",
            backgroundColor: "white",
          }}
        >
          <div>
            <div style={{ backgroundColor: "#222E50", color: "white" }}>
              <h6 className="minicart-title">
                {translate("ShoppingCart")}
                <i
                  className="material-icons"
                  onClick={() => close()}
                  style={{ cursor: "pointer", fontSize: "38px" }}
                >
                  close
                </i>
              </h6>
            </div>

            <div className={`minicat-content-wrapper scroll-bar-thin-style`}>
              {renderMiniCartContent()}
            </div>
            {basketQuoteProductsState.length === 0
              ? renderTotalPriceSection()
              : null}
          </div>
          {basketQuoteProductsState.length === 0
            ? renderCheckoutButton()
            : null}
        </div>
      </div>
    </>
  )
}
