import React, { useState } from "react"
import { PREVIEW } from "../../project-config.js"

import { HEAD_SEARCH } from "../../redux/links"

import { I18nContext } from "../../i18n/index"
import { navigate } from "gatsby"

export default function SearchHelper({ setSearchActive, searchActive }) {
  const { translate } = React.useContext(I18nContext)
  const [keywords, setKeywords] = useState("")
  const [finalRecording, setFinalRecording] = useState("")
  const [searchInput, setSearchInput] = useState("")
  const [searchInputFocused, setSearchInputFocused] = useState(false)

  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  })

  const options = {
    autoStart: false,
    continuous: false,
  }

  const handleSubmit = evt => {
    evt.preventDefault()
    if (evt.target.tagName == "P") {
      navigate(`${PREVIEW}/search/${evt.target.textContent}`)
    } else {
      navigate(`${PREVIEW}/search/${searchInput}`)
    }
  }

  const helpSearch = e => {
    e.persist()

    const { value } = e.target
    if (finalRecording != "") {
      setFinalRecording("")
    }
    setSearchInput(value)
    if (searchInput.length > 1) {
      let link = HEAD_SEARCH.replace("$keyword", searchInput)
      fetch(link).then(res => {
        res.text().then(text => {
          console.error("WED2", text)
          text = text.split("\n")
          setKeywords(text)
        })
      })
    }
    if (value == "") {
      setKeywords("")
    }
  }

  const toggleDrawer = (side, open) => event => {
    //console.error("CUMA", side, open);
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return
    }

    setState({ ...state, [side]: open })
    setKeywords("")
  }
  console.info(searchActive, "kaka")

  return (
    <div
      className={
        searchInputFocused
          ? "search-form-container active"
          : "search-form-container"
      }
      aria-expanded={searchInputFocused}
    >
      <form
        id="searchForm"
        onSubmit={handleSubmit}
        className={searchInputFocused ? "active" : ""}
      >
        <label htmlFor="search-input">
          <svg
            style={{ color: "#222E50" }}
            width="24"
            height="22"
            viewBox="0 0 24 22"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M15.9062 14.6562L12.0938 10.8438C12.0312 10.7812 11.9479 10.75 11.8438 10.75H11.4062C11.9062 10.1667 12.2917 9.51042 12.5625 8.78125C12.8542 8.05208 13 7.29167 13 6.5C13 5.3125 12.7083 4.22917 12.125 3.25C11.5417 2.25 10.75 1.45833 9.75 0.875C8.77083 0.291667 7.6875 0 6.5 0C5.3125 0 4.21875 0.291667 3.21875 0.875C2.23958 1.45833 1.45833 2.25 0.875 3.25C0.291667 4.22917 0 5.3125 0 6.5C0 7.6875 0.291667 8.78125 0.875 9.78125C1.45833 10.7604 2.23958 11.5417 3.21875 12.125C4.21875 12.7083 5.3125 13 6.5 13C7.29167 13 8.05208 12.8646 8.78125 12.5938C9.51042 12.3021 10.1667 11.9062 10.75 11.4062V11.8438C10.75 11.9271 10.7812 12.0104 10.8438 12.0938L14.6562 15.875C14.7188 15.9583 14.8021 16 14.9062 16C15.0104 16 15.1042 15.9583 15.1875 15.875L15.9062 15.1875C15.9688 15.1042 16 15.0104 16 14.9062C16 14.8021 15.9688 14.7188 15.9062 14.6562ZM6.5 11.5C5.60417 11.5 4.77083 11.2812 4 10.8438C3.22917 10.3854 2.61458 9.77083 2.15625 9C1.71875 8.22917 1.5 7.39583 1.5 6.5C1.5 5.60417 1.71875 4.77083 2.15625 4C2.61458 3.22917 3.22917 2.625 4 2.1875C4.77083 1.72917 5.60417 1.5 6.5 1.5C7.39583 1.5 8.22917 1.72917 9 2.1875C9.77083 2.625 10.375 3.22917 10.8125 4C11.2708 4.77083 11.5 5.60417 11.5 6.5C11.5 7.39583 11.2708 8.22917 10.8125 9C10.375 9.77083 9.77083 10.3854 9 10.8438C8.22917 11.2812 7.39583 11.5 6.5 11.5Z"
              fill="#222E50"
            />
          </svg>
        </label>
        <input
          onFocus={() => {
            setSearchInputFocused(true)
            setSearchActive(true)
          }}
          onBlur={() => {
            setSearchInputFocused(false)
            setSearchActive(false)
          }}
          type="text"
          className="search-form-input"
          name="keyword"
          id="search-input"
          autoComplete="off"
          onChange={e => helpSearch(e)}
          placeholder={translate("js.header.searchfor")}
          value={searchInput}
        />
      </form>
    </div>
  )
}
