import {
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
  LOGOUT_SUCCESS,
  LOGOUT_FAILURE,
  UPDATE_LOGIN,
  UPDATE_LOGIN_FB,
  USER_DATA_SUCCESS,
  COOKIE_WINDOW_SHOW
} from "../types.js";

const initialState = {
  loginName: "",
  securityToken: "",
  lat: 40.6976701,
  lng: -74.2598696,
  userData: [],
  userInfo: {
    default: true,
    countryCode: "ca",
    countryName: "Canada",
    city: "Barrie",
    lat: 40.6976701,
    lng: -74.2598696,
    regioncode: "ON",
    regionName: "Ontario"
  },
  cookieWindow: null
};

const loginReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case USER_DATA_SUCCESS:
      return {
        ...state,
        userInfo: {
          default: false,
          countryCode: payload.countrycode.toLowerCase(),
          countryName: payload.countryname,
          city: payload.city,
          lat: payload.latitude,
          lng: payload.longitude,
          regioncode: payload.regioncode,
          regionName: payload.regionname
        }
      };
    case UPDATE_LOGIN:
      return {
        ...state,
        loginName: payload.loginName ? payload.loginName : "",
        securityToken: payload.securityToken ? payload.securityToken : "",
        lat: payload.lat ? payload.lat : 40.6976701,
        lng: payload.lng ? payload.lng : -74.2598696
      };
    case UPDATE_LOGIN_FB:
      console.info("ses", payload);
      return {
        ...state,
        loginName: payload.loginName ? payload.loginName : "",
        securityToken: payload.securityToken ? payload.securityToken : ""
      };
    case LOGIN_FAILURE:
      return { ...state, errorMessageLogin: payload };
    case LOGIN_SUCCESS:
      return {
        ...state,
        loginName: payload.loginName ? payload.loginName : "",
        securityToken: payload.securityToken ? payload.securityToken : "",
        lat: payload.lat ? payload.lat : 40.6976701,
        lng: payload.lng ? payload.lng : -74.2598696
      };
    case LOGOUT_SUCCESS:
      return {
        ...state,
        loginName: "",
        securityToken: ""
      };

    case COOKIE_WINDOW_SHOW:
      return {
        ...state,
        cookieWindow: payload
      };
    case LOGOUT_FAILURE:
      return { ...state, errorMessageLogout: payload };
    default:
      return state;
  }
};

export default loginReducer;
