class FetchFunction {
  api = language =>
    fetch(BASKET_LINK.replace("$LANGUAGE", language))
      .then(res => res.json())
      .then(json => {
        let basket = json.__Result
        console.info(
          "fetch basket",
          json.__Result,
          language,
          BASKET_LINK.replace("$LANGUAGE", language)
        )
        return basket
      })
      .catch(error => {
        console.warn("Server respond failed", error)
      })
}

export function* fetchBasketSaga() {
  console.log("fetchBasket")
  let language = store.getState().mainReducer.lang
  const api = new FetchFunction()

  const basket = yield call(api.api, language)
  console.log("Result Saga", basket)
  yield put(fetchBasketAction(basket))
}
